import React from 'react';
import './App.css';

function App() {
  const results = [];
  fetch("https://fetch-hiring.s3.amazonaws.com/hiring.json", {
   
    mode: "cors",
    headers: {
      "Content-Type": "application/json",
    }
   }).then((res) => {
      return res.json();
    }).then((data) => {
      console.log(data)
      data.map(d => {
        results.push(d.name)
      })
      console.log(results)
    })
  return (
    <div className="App">
      <header className="App-header">Fetch Rewards - Display Results</header>
      {results.map(a =>
        <p>{a.name}</p>)}

    </div>
  );
}

export default App;
